
// ==============================
// Custom style
// ==============================

// === Base Typography ===
body {
  line-height: 1.8;
}

strong {
  font-weight: 700;
}

// === Theme Variables ===
:root {
  --theme-color-light: #ffffff;
  --theme-color-dark: #121212;
  --icon-color: #ff6600;
  --tile-color: #007acc;
}

// ==============================
// Light Mode (system preference)
// ==============================
@media (prefers-color-scheme: light) {
  body {
    background-color: var(--theme-color-light);
    color: #333;
  }

  th {background-color:teal !important}

  .about-me-bg {
    background-image: url('/logo.png');
  }
}

// ==============================
// Dark Mode (system preference)
// ==============================
@media (prefers-color-scheme: dark) {
  body {
    background-color: #0f0f10 !important;
    color: #ffffff !important;
  }

  .about-me-bg {
    background-image: url('/logo-light.png');
  }

  td {
      color: #e0f0ff !important; // or use any light color that fits your theme
    }
  
  th {
      color: #ffffff !important;
    } 

}


// ==============================
// Dark Mode (manual override)
// ==============================
[theme="dark"] {
  body {
    background-color: #0f0f10 !important;
    color: #e0e0e0 !important;
  }

  h1 { color: #7EB6FF !important; }
  h2 { color: #AFCBFF !important; }
  h3 { color: #BFDFFF !important; }
  h4, h5, h6 { color: #D0E8FF !important; }

  code {
    background-color: #1c1c1e !important;
    color: #ffd580 !important;
  }

  blockquote {
    background-color: #1e1e1e !important;
    border-left: 4px solid #444 !important;
    color: #ccc !important;
  }

  table {
    background-color: #1a1a1a !important;
    color: #e0e0e0 !important;
  }
  
  td:first-child strong {
    color: #e0f0ff !important;
  }

  th, td {
    border-color: #333 !important;
  }

  th {background-color:teal !important}

  td[style*="background-color:#f3f3f3"],
  td[style*="background-color:#cacaca"] {
    color: #222222 !important;
  }

  div[style*="background-color:#F0F8FF"] {
    background-color: #222831 !important;
    color: #D0D0D0 !important;
  }

  div[style*="border-left"] {
    border-left: 4px solid #4E7D96 !important;
  }

  div[style*="border-left: 5px solid #003366"],
  div[style*="border-left: 4px solid #002D62"] {
    border-left: 5px solid #4E7D96 !important;
  }

  div[style*="text-align: center"] {
    border-left: none !important;
  }

  .unwanted-section {
    background-color: inherit !important;
    border-left: none !important;
  }

  .about-me-bg {
    background-image: url('/logo-light.png');
  }

}

// ==============================
// Utility / Custom Sections
// ==============================
.about-me-bg {
  background-repeat: no-repeat;
  background-size: 200px;
  background-position: right bottom;
  padding: 10px 20px;
  text-align: left;
}

.single-title {
  margin-bottom: 50px !important;
}

/* === Light Mode Summary Box === */
.summary-box {
  background-color: #F0F8FF;
  border-left: 5px solid #003366;
  padding: 12px;
  margin-bottom: 20px;
  color: #002244;
}

/* === Dark Mode Summary Box === */
[theme="dark"] .summary-box {
  background-color: #1f2a38;
  border-left-color: #66b2ff;
  color: #e0f0ff;
}



